<template>
  <section>
    <div class="page-header">
      <h3 class="page-title">
        Appointments
      </h3>
      <nav aria-label="breadcrumb">
        <ol class="breadcrumb">
          <li class="breadcrumb-item">
            <a href="javascript:void(0);">Appointment</a>
          </li>
          <li class="breadcrumb-item active" aria-current="page">all</li>
        </ol>
      </nav>
    </div>
    <div class="card">
      <div class="card-body">
        <b-table hover responsive :items="items" :fields="fields">
          <template #cell(status)="data">
            <div>
              <label
                class="font-weight-bold badge"
                :class="
                  data.value == 'Cancelled'
                    ? 'badge-danger'
                    : data.value == 'Approved'
                    ? 'badge-success'
                    : 'badge-warning'
                "
                >{{ data.value }}</label
              >
            </div>
          </template>
          <template #cell(action)="data">
            <div>
              <span v-if="data.item.status != 'Cancelled'" class="mr-1">
                <b-button
                  v-if="data.item.status != 'Approved'"
                  variant="success"
                  size="sm"
                  v-b-modal.modal-approved
                >
                  Approved
                </b-button>

                <b-button variant="danger" size="sm" v-b-modal.modal-cancelled>
                  Cancel
                </b-button>
              </span>
              <b-button
                variant="primary"
                size="sm"
                @click="sendEmail(data.item)"
              >
                Send email
              </b-button>
            </div>
          </template>
        </b-table>
      </div>
    </div>

    <b-modal id="modal-approved" title="Approved">
      <div class="text-center">
        <div class="font-weight-bold">Are you sure to approve?</div>
      </div>
    </b-modal>
    <b-modal id="modal-cancelled" title="Approved">
      <div class="text-center">
        <div class="font-weight-bold">Are you sure to approve?</div>
      </div>
    </b-modal>
  </section>
</template>

